<template>
  <div v-loading="loading" element-loading-text="">
    <div class="handle-box">
      <div class="btn">
        <el-button type="primary" @click="addRole"></el-button>
      </div>
    </div>

    <el-table
      :data="tableData"
      border
      :header-cell-style="{ background: '#f0f0f0' }"
    >
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            @click="handleEdit(scope.$index, scope.row)"
            ></el-button
          >
          <el-button
            type="danger"
            size="mini"
            @click="handleDel(scope.$index, scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      :visible.sync="dialogDel"
      title=""
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px"></div>
      <span>
        <el-button type="primary" @click="toDel"></el-button>
        <el-button type="danger" @click="dialogDel = false"></el-button>
      </span>
    </el-dialog>

    <div class="block">
      <el-pagination
        @current-change="currentChange"
        :current-page.sync="current"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="size"
        layout="sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="sizeChange"
      ></el-pagination>
    </div>

    <el-dialog
      title=""
      :visible.sync="dialogRole"
      width="50%"
      :close-on-click-modal="false"
    >
      <div class="box">
        <el-form :model="form" label-width="120px">
          <el-form-item label="">
            <el-input
              v-model="form.title"
              placeholder=""
              :disabled="disabledRole"
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-checkbox v-model="checkAll" @change="AllChange"
              ></el-checkbox
            >
            <div class="permission">
              <el-checkbox-group
                v-model="form.permissions"
                class="permission-item"
              >
                <el-checkbox label="resident" @change="oneChange" border>
                  <span style="font-weight: bold"></span>
                </el-checkbox>
                <el-checkbox label="residentGet" @change="oneChange"
                  ></el-checkbox
                >
              </el-checkbox-group>
            </div>
            <div class="permission">
              <el-checkbox-group
                v-model="form.permissions"
                class="permission-item"
              >
                <el-checkbox label="house" @change="oneChange" border>
                  <span style="font-weight: bold"></span>
                </el-checkbox>
                <el-checkbox label="houses" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="housesAdd" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="housesGet" @change="oneChange"
                  ></el-checkbox
                >
              </el-checkbox-group>
            </div>
            <div class="permission">
              <el-checkbox-group
                v-model="form.permissions"
                class="permission-item"
              >
                <el-checkbox label="picture" @change="oneChange" border>
                  <span style="font-weight: bold"></span>
                </el-checkbox>
                <el-checkbox label="banner" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="bannerAdd" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="bannerGet" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="bannerDel" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="document" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="documentAdd" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="documentGet" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="documentDel" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="documentEdit" @change="oneChange"
                  ></el-checkbox
                >
              </el-checkbox-group>
            </div>
            <div class="permission">
              <el-checkbox-group
                v-model="form.permissions"
                class="permission-item"
              >
                <el-checkbox label="message" @change="oneChange" border>
                  <span style="font-weight: bold"></span>
                </el-checkbox>
                <el-checkbox label="message" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="messageAdd" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="messageGet" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="messageDel" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="messageEdit" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="typeAdd" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="typeGet" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="typeDel" @change="oneChange"
                  ></el-checkbox
                >
              </el-checkbox-group>
            </div>
            <div class="permission">
              <el-checkbox-group
                v-model="form.permissions"
                class="permission-item"
              >
                <el-checkbox label="permission" @change="oneChange" border>
                  <span style="font-weight: bold"></span>
                </el-checkbox>
                <el-checkbox label="roleAdd" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="roleDel" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="roleEdit" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="roleGet" @change="oneChange"
                  ></el-checkbox
                >
              </el-checkbox-group>
            </div>
            <div class="permission" style="margin-left: 140px">
              <el-checkbox-group
                v-model="form.permissions"
                class="permission-item"
              >
                <el-checkbox label="manageAdd" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="manageDel" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="manageEdit" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="manageGet" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="manageResetPwd" @change="oneChange"
                  ></el-checkbox
                >
              </el-checkbox-group>
            </div>
            <div class="permission">
              <el-checkbox-group
                v-model="form.permissions"
                class="permission-item"
              >
                <el-checkbox label="buy" @change="oneChange" border>
                  <span style="font-weight: bold"></span>
                </el-checkbox>
                <el-checkbox label="buyAdd" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="compileBuy" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="orderServe" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="checkBuy" @change="oneChange"
                  ></el-checkbox
                >
                <el-checkbox label="delBuy" @change="oneChange"
                  ></el-checkbox
                >
              </el-checkbox-group>
            </div>
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="newRole"></el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/index.js";
import { log } from "util";

export default {
  data() {
    return {
      loading: true,
      dialogRole: false,
      form: {
        title: "",
        name: "",
        permissions: [],
      },
      stateList: [
        {
          label: "",
          value: 1,
        },
        {
          label: "",
          value: 0,
        },
      ],
      tableData: [],

      checkAll: false,
      permissionList: [
        "resident", // 
        "residentGet", // 

        "house", // 
        "houses",
        "housesAdd", // 
        "housesGet", // 

        "picture", // 
        "banner", // 
        "bannerAdd",
        "bannerGet",
        "bannerDel",
        "message", // 
        "messageAdd",
        "messageGet",
        "messageDel",
        "messageEdit",
        "typeAdd",
        "typeGet",
        "typeDel",
        "document", // 
        "documentAdd",
        "documentGet",
        "documentDel",
        "documentEdit",

        "buy", // 
        "buyAdd", // 
        "compileBuy", // 
        "orderServe", // 
        "delBuy", // 
        "checkBuy", // 
        "suspected", // 

        // "total", // 
        // "rentersTotal",
        "permission", // 
        "roleGet",
        "roleAdd",
        "roleEdit",
        "roleDel",
        "manageGet",
        "manageAdd",
        "manageEdit",
        "manageDel",
        "manageResetPwd", // 
        "switch",
        "refund",
      ],
      areaPerson: [
        "resident", // 
        "residentGet", // 

        "house", // 
        "houses",
        "housesAdd", // 
        "housesGet", // 

        "picture", // 
        "banner", // 
        "bannerAdd",
        "bannerGet",
        "bannerDel",

        "message", // 
        "messageAdd",
        "messageGet",
        "messageDel",
        "messageEdit",
        "typeAdd",
        "typeGet",
        "typeDel",

        "document", // 
        "documentAdd",
        "documentGet",
        "documentDel",
        "documentEdit",

        "suspected", // 

        "permission", // 
        "roleGet",
        "roleAdd",
        "roleEdit",
        "roleDel",
        "manageGet",
        "manageAdd",
        "manageEdit",
        "manageDel",
        "manageResetPwd", // 
      ],

      dialogDel: false,
      id: "", // id
      disabledRole: false,
      // 
      current: 1,
      size: 10,
      total: 0,
      role: localStorage.getItem("role"),
    };
  },
  mounted() {
    this.getRoles();
  },
  methods: {
    getRoles() {
      var self = this;
      API.getRole(self.current)
        .then((res) => {
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    // 
    currentChange(val) {
      var self = this;
      self.current = val;
      self.loading = true;
      API.getRole(val, self.size)
        .then((res) => {
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    // 
    sizeChange(val) {
      var self = this;
      self.size = val;
      self.loading = true;
      API.getRole(self.current, val)
        .then((res) => {
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },

    addRole() {
      var self = this;
      self.dialogRole = true;
      self.form = {
        title: "",
        name: "",
        permissions: [],
      };
      self.disabledRole = false;
      self.checkAll = true;
      if (self.checkAll === true) {
        self.form.permissions = self.checkAll ? self.permissionList : [];
      }
    },
    newRole() {
      var self = this;
      self.form.name = self.form.title;

      API.role(self.form).then((res) => {
        self.dialogRole = false;
        self.$message.success("");
        self.getRoles();
        self.current = 1;
        self.form = {};
        self.form.permissions = [];
      });
    },
    // 
    handleEdit(index, row) {
      var self = this;
      self.dialogRole = true;
      self.disabledRole = true;
      self.form = row;
      self.form.permissions = row.permissions;
      self.checkAll = row.permissions.length >= self.permissionList.length;
    },
    AllChange(val) {
      var self = this;
      if (
        localStorage.getItem("username") == "admin" &&
        self.form.name == "admin"
      ) {
        self.form.permissions = val ? self.permissionList : [];
      } else {
        self.form.permissions = val ? self.areaPerson : [];
      }
    },
    oneChange(val) {
      var self = this;
      console.log(self.form.permissions);
      self.checkAll =
        self.form.permissions.length >= self.permissionList.length;
    },

    handleDel(index, row) {
      var self = this;
      self.dialogDel = true;
      self.id = row.id;
    },
    toDel() {
      var self = this;
      API.delRole(self.id).then((res) => {
        self.$message.success("");
        self.dialogDel = false;
        self.getRoles();
        self.current = 1;
      });
    },
  },
};
</script>

<style scoped>
.permission {
  display: flex;
  flex-wrap: wrap;
}

.permission-item {
  margin: 10px;
  padding: 0 10px;
}
</style>
